import * as React from 'react';
import {OwnedNft} from 'alchemy-sdk';

export const WrappedReaperOwnedNft = React.memo(
  function WrappedReaperOwnedNft({
    ownedNft,
    onClickBurn,
  }: {
    readonly ownedNft: OwnedNft;
    readonly onClickBurn: () => void;
  }): JSX.Element {
    return (
      <div className="card" style={{paddingBottom: 0}}>
        <div className="font-mono uppercase text-center text-xl lg:text-base xl:text-xl pb-6">
          {ownedNft.title}
        </div>
        <img
          alt={ownedNft.title}
          src={ownedNft.media?.[0]?.raw}
        />
        <div className="w-full grid grid-cols-1">
          <button
            children="Burn"
            onClick={onClickBurn}
            className="font-bold text-3xl uppercase bg-black text-white inline-block w-full text-center p-2"
          />
        </div>
      </div>
    );
  }
);
