import * as React from 'react';

import {WrappedReaperState, Stateful} from '../@types';
import {maybeWrappedReaperResult} from '../utils';

export const WrappedReaperAttribute = React.memo(
  function WrappedReaperAttribute<FieldName extends keyof WrappedReaperState>({
    fieldName,
    wrappedReaperResult,
    stringify,
  }: {
    readonly fieldName: FieldName;
    readonly wrappedReaperResult: Stateful<WrappedReaperState>;
    readonly stringify: (value: WrappedReaperState[FieldName]) => string;
  }): JSX.Element {
    const maybeResult = maybeWrappedReaperResult(wrappedReaperResult, fieldName);
    return (
      <div className="text-3xl lg:text-5xl font-bold mt-2">
        {maybeResult ? stringify(maybeResult) : "-"}
      </div>
    );
  }
);
